//import React from 'react';
import * as BABYLON from 'babylonjs';
import 'babylonjs-loaders';
import * as BGUI from 'babylonjs-gui';

export const addFaceLinkedObj = (scene, fPivot) => {
  const MODEL_PATH = "./models/";

  const lensMat = new BABYLON.StandardMaterial("lensMat", scene);
  lensMat.diffuseColor = BABYLON.Color3.Black();
  lensMat.alpha = .5;
  const frameMat = new BABYLON.StandardMaterial("frameMat", scene);
  frameMat.diffuseColor = BABYLON.Color3.Black();
  const armMat = new BABYLON.StandardMaterial("armMat", scene);
  armMat.diffuseColor = BABYLON.Color3.Black();
  armMat.alpha = .3;

  const wireMat = new BABYLON.StandardMaterial("wireMat", scene);
  wireMat.emissiveColor = new BABYLON.Color3(0/255, 255/255, 255/255);
  wireMat.wireframe = true;
  wireMat.alpha = 0; //.05;

  const pos = { x: .02, y: .1, z: .0};  // position adjustment

  // Face mesh
  BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "face.obj", scene, meshes => {
    const sc = .008;
    meshes.forEach(m => {
      // console.log(m.name);
      m.material = wireMat;
      m.rotation.x = 20 * Math.PI / 180;
      m.rotation.y = Math.PI;    // model initially posed backward
      m.scaling  = new BABYLON.Vector3(sc, sc, sc);
      fPivot.addChild(m);
      m.position.set(pos.x, pos.y, pos.z);
    });
  });

  // Sunglass mesh
  BABYLON.SceneLoader.ImportMesh("", MODEL_PATH, "BoseAlto.obj", scene, meshes => {
    const sc = 7.4;
    meshes.forEach(m => {
      console.log(m.name);
      //m.rotation.x = -5 * Math.PI / 180;  // negative looks up
      m.rotation.y =  Math.PI;     // model initially posed backward
      m.scaling  = new BABYLON.Vector3(sc, sc, sc);
      if (m.name === "hdrSkyBox") {
        m.dispose();
      }
      if (m.name === "_mm1") {
        m.material = lensMat;
      } else if (m.name.includes("_Frames")) {
        m.material = frameMat;
      } else {
        m.material = armMat;
      }
      fPivot.addChild(m);
      m.position.set(pos.x, pos.y + .18, pos.z);
    });
  });

  const advTexture = BGUI.AdvancedDynamicTexture.CreateFullscreenUI("ui1");
  const panel = new BGUI.StackPanel();
  panel.width = "220px";
  panel.fontSize = "14px";
  panel.horizontalAlignment = BGUI.Control.HORIZONTAL_ALIGNMENT_RIGHT;
  panel.verticalAlignment   = BGUI.Control.VERTICAL_ALIGNMENT_BOTTOM;
  advTexture.addControl(panel);

  const header = new BGUI.TextBlock();
  header.text = "Lens Opacity / Color";
  header.height = "40px";
  header.color = "white";
  header.textHorizontalAlignment = BGUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  header.paddingTop = "10px";
  panel.addControl(header);
  
  const slider = new BGUI.Slider();
  slider.horizontalAlignment = BGUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  slider.minimum = 0;
  slider.maximum = 1;
  slider.color = "green";
  slider.value = .5;
  slider.height = "20px";
  slider.width  = "180px";
  slider.onValueChangedObservable.add(value => { // value is a color3
    lensMat.alpha = value;
  });
  panel.addControl(slider);

  const picker = new BGUI.ColorPicker();
  picker.horizontalAlignment = BGUI.Control.HORIZONTAL_ALIGNMENT_LEFT;
  picker.value = lensMat.diffuseColor;
  //picker.value = sphereMaterial.diffuseColor;
  picker.height = "150px";
  picker.width  = "150px";
  picker.onValueChangedObservable.add(value => { // value is a color3
    lensMat.diffuseColor = value;
  });
  panel.addControl(picker);  

  const btn1 = BGUI.Button.CreateSimpleButton("button1", "Product Info");
  btn1.width  = "160px";
  btn1.height = "30px";
  //btn1.fontSize = 100;
  btn1.color  = "white";
  btn1.cornerRadius = 20;
  btn1.background   = "blue";
  btn1.onPointerUpObservable.add(() => {
    console.log(picker.value);
    const r = Math.round(picker.value.r * 256);
    const g = Math.round(picker.value.g * 256);
    const b = Math.round(picker.value.b * 256);
    window.alert(`Product Info: Bose Alto, Lens Color=R:${r},G:${g},B:${b}`);
  });
  panel.addControl(btn1);

  const btn2 = BGUI.Button.CreateSimpleButton("button2", "BUY");
  btn2.width  = "160px";
  btn2.height = "30px";
  btn2.color  = "white";
  btn2.cornerRadius = 20;
  btn2.background   = "green";
  btn2.onPointerUpObservable.add(() => {
    window.open("https://classian.myshopify.com/products/bose-frames-audio-sunglasses-with-open-ear-headphones-black-with-bluetooth-connectivity");
  });
  panel.addControl(btn2);
}

export const mouthOpen = (scene, level) => {
  const m = scene.getMeshByName("morphbase");
  if (m) {
    m.material.alpha = (m.material.alpha === 0) ? .05 : 0;
  }
}
