import React from 'react';
import './App.scss';
import { runJeelizFaceFilter } from './jeelizFF3D.js';  // 2D (Canvas WebGL) or 3D (Babylon)

const App: React.FC = () => {
  React.useEffect(() => {
    runJeelizFaceFilter();
  }, []);

  return (
    <div className="App">
      <canvas width="800" height="800" id="jeeFaceFilterCanvas" />
      <p>Try to open your mouth to toggle the facial wireframe.</p>
    </div>
  );
}

export default App;
